.map {
  color: $dark;
  background-color: $light;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr $max-width 1fr;
    grid-template-rows: 95px max-content 105px;
  }
}

.map__cooperation-address {
  display: flex;
  justify-content: space-between;
  padding: 26px 20px;

  @media (min-width: $tablet-width) {
    padding: 60px 70px;
  }

  @media (min-width: $desktop-width) {
    max-width: 565px;

    display: flex;
    justify-content: space-between;
    background-color: $light;

    grid-column: 2 / 3;
    grid-row: 2 / 3;
    z-index: 1;
  }
}

.map__cooperation-address--catalog {
  background-color: $light-grey;

  @media (min-width: $desktop-width) {
    background-color: $light;
  }
}

.map__cooperation-title {
  font-family: $oswald;
  color: $dark-2;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 26px;
  }
}

.map__address {
  align-items: end;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }

  @media (min-width: $desktop-width) {
    align-items: start;
  }
}

.map__address-text {
  color: $dark-grey;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;

  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    text-align: right;

    max-width: 160px;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.map__location {
  height: 362px;

  @media (min-width: $tablet-width) {
    height: 400px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
  }
}
